.team-modal {
  z-index: 100;
  width: 100vw;
  height: 100%;
  opacity: 0;
  pointer-events: none;
  justify-content: center;
  align-items: center;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

