.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.loader {
  height: 100%;
  width: 100%;
  z-index: 9;
  background: #fff;
  position: fixed;
  top: 216px;
  bottom: 0;
  left: 0;
  right: 0;
}

.loader .loader-inner {
  clip: rect(0, 80px, 80px, 40px);
  height: 80px;
  width: 80px;
  animation: animate 1.5s linear infinite;
  position: absolute;
  top: 35%;
  left: calc(50% - 40px);
}

@keyframes animate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(220deg);
  }
}

.loader .loader-inner:after {
  clip: rect(0, 80px, 80px, 40px);
  content: "";
  height: 80px;
  width: 80px;
  border-radius: 50%;
  animation: animate2 1.5s ease-in-out infinite;
  position: absolute;
}

@keyframes animate2 {
  0% {
    transform: rotate(-140deg);
    box-shadow: inset 0 0 0 17px #ff6b08;
  }

  50% {
    box-shadow: inset 0 0 0 2px #ff6b08;
  }

  100% {
    transform: rotate(140deg);
    box-shadow: inset 0 0 0 17px #ff6b08;
  }
}

