h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}
button {
  cursor: pointer;
  padding: 0;
  margin: 0;
  border: none;
}
